.index {
  height: 100vh;
  min-height: 100vh;
  .content {
    width: 100%;
    height: 100%;
    >div {
      &.product {

      }
      &.study {

      }
      &.mine {
        height: 100%;
        background-color: #F6F6F5;
        >div {
          >.header {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 5.6rem;
            margin-bottom: 0.4rem;
            background: #FFDC20 url('/static/images/mine_bg.png') no-repeat center/100% 100%;
            >div {
              text-align: center;
              >.avatar {
                >img {
                  width: 2rem;
                  height: 2rem;
                  border-radius: 2rem;
                  border: 0.08rem solid #FFC320;
                }
              }
              >.name {
                line-height: 0.8rem;
                font-size: 0.48rem;
                color: #824E00;
                font-weight: bold;
              }
            }
          }
          >.menu {
            >.item {
              margin-bottom: 0.4rem;
            }
          }
        }
      }
    }
  }
}